*{
    margin: 0;
    padding: 0;
    border: 0;
}

.valueHolder{
    width: 7rem;
    height: 4rem;
    margin: 1rem;
    box-shadow: 1px 1px 3px grey;
    border-radius: 4px;
    /* margin-bottom: 10px; */
    /* background-color: coral; */
}

.value{
    height: 3rem;
    width: 4.5rem;
    /* margin: 0.2rem; */
    padding: 0.5rem 0 0.5rem 0.4rem;
    float: left;
    /* background-color: grey; */
}

.valueName{
    font-size: 0.7rem;
    color: grey;
    /* margin-left: 1px; */
}

.actualValue{
    font-size: 2rem;
    color: grey;
}

.justify{
    height: 3rem;
    width: 2rem;
    float: left;
    padding: 0.2rem 0 0.5rem 0;

    /* background-color: hotpink; */
}

.justifyBtn{
    background-color: rgba(0, 0, 0, 0);
    color: grey;
    height: 1.5rem;
    width: 2rem;
    padding-top: auto;
    padding-left: 10px;
    padding-right: 10px;
    font-size: 1.5rem;
    text-align: center;
    line-height: 2rem;
}
