*{
    margin: 0;
    padding: 0;
    border: 0;
}

.skillHolder{
    margin: 1rem 0 1rem 0;
}


.skillTitle{
    border-radius: 8px;
    margin: 0.5rem;
    padding: 1.2rem;
    background-color: rgb(246, 246, 246);
    box-shadow: 2px 2px 3px grey;
    font-size: 1.2rem;
}

.skillList{
    
    border-radius: 3px;
    box-shadow: 1px 1px 1px rgb(214, 214, 214);
    margin: 0 1.2rem 0 1.2rem;
}